html,
body {
  position: relative;
  font-family: "SinkinSansRg", Helvetica, Arial, sans-serif; }

body {
  padding-bottom: 110px; }

ul {
  list-style: none;
  padding-left: 0; }

a {
  color: #494949;
  transition: ease .3s; }
  a, a:hover, a:focus {
    text-decoration: none; }
  a:hover, a:focus {
    color: #9b59b6; }

p {
  font-size: 16px;
  line-height: 1.8; }

.header {
  padding: 30px 60px; }
  .header__logo {
    width: 202px;
    float: left;
    margin-top: 15px; }

.menu {
  margin-top: 40px; }
  .menu__list-item {
    display: inline-block;
    position: relative; }
  .menu__link {
    margin-right: 15px;
    margin-left: 15px;
    font-size: 14px;
    padding: 15px 0;
    text-align: center; }
    .menu__link:after {
      content: ' ';
      display: inline-block;
      position: absolute;
      bottom: -8px;
      left: 50%;
      transform: translateX(-50%);
      box-shadow: inset 3px 0px 3px 0px rgba(255, 255, 255, 0.4);
      width: 0;
      height: 2px;
      background-color: rgba(7, 161, 153, 0.5);
      transition: ease .3s; }
    .menu__link.no-after:after {
      display: none; }
    .menu__link:hover:after {
      width: 80%; }
  .menu__btn {
    margin-right: 0;
    margin-left: 15px; }
  .menu__icon {
    display: block;
    position: relative;
    top: 0px;
    float: left;
    left: 15px;
    margin-right: 5px; }
    .menu__icon_submenu {
      font-size: 18px;
      position: relative;
      top: 2px;
      right: 0; }

.btn {
  display: inline;
  padding: 10px 50px;
  border-radius: 8px;
  color: #ffffff;
  transition: .3s ease; }
  .btn:hover {
    background-color: #494949;
    color: #ffffff; }
  .btn_green {
    background-color: #07a199; }

.submenu {
  position: absolute;
  top: 30px;
  left: 0px;
  width: 100%;
  z-index: 1;
  display: block;
  background-color: #9b59b6;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-bottom: none;
  display: none; }
  .menu__list-item:hover .submenu {
    border-bottom: 2px solid #713b87; }
  .submenu__link {
    color: #d0b2dd;
    display: block;
    padding: 10px 30px;
    border-bottom: 2px solid #713b87;
    font-size: 12px; }
    .submenu__list-item:last-child .submenu__link {
      border: none; }
    .submenu__link:hover {
      color: #fff;
      background-color: #804399; }

.spot {
  padding: 0 5px 15px 0px;
  float: left; }

.spot-subpage {
  margin: 60px -40px 50px -40px; }

.spot-content {
  height: 542px;
  position: relative; }

